
import { Component, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { AuthService } from 'src/app/services/auth/auth.service';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ToastService } from 'src/app/services/toast/toast.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pairs',
  templateUrl: './pairs.page.html',
  styleUrls: ['./pairs.page.scss'],
})
export class PairsPage implements OnInit {

  content_loaded: boolean = false; name: any;
  wallets: any;
  constructor(
    private authService: AuthService,
    private loadingController: LoadingController,
    private formBuilder: UntypedFormBuilder,
    private toastService: ToastService,
    private router: Router
  ) { }

  ngOnInit() {




    this.loaddata().then(console.log);



    this.authService
      .getme()
      .subscribe((data) => {
        this.name = data.name;
        // this.edit_profile_form.get('name').setValue(data.name);

      });




  }

  async loaddata() {


    const loading = await this.loadingController.create({
      cssClass: 'default-loading',
      message: 'Loading... Please be patient.',
      spinner: 'crescent'
    });


    await loading.present();


    this.authService
      .wallets()
      .subscribe((data) => {
        this.wallets = data;
        console.log(data);


        this.content_loaded = true;
        loading.dismiss();

      });

  }


  
  sendto(coin: any ) {

 
    


    this.router.navigate([ "/trade",coin]);

  }
}